import React, { useState } from 'react'

import UseTitle from './UseTitle';
const UseTitle1 = () => {

    const [count, setCount] = useState(0);
    UseTitle(count);
    return (
        <div>
            <h1>ggggg</h1>
            <button onClick={() => { setCount(count + 1) }}>Click Me {count}</button>
        </div>
    );
}
export default UseTitle1; 
